/*Modal*/
.model{
  position:fixed;
  top:50%;
  left:50%;
  transform:translate(-50%, -50%);
  width:101%;
  height:101%;

  background-color: rgba(0, 0, 0, 0.774) ;
  z-index:100;
}
.modal-content{
  position:absolute;
  width: 50%;
  height:70%;

  left:25%;

  top:15% !important;
}

.modal-body{
  overflow:scroll;
  overflow-x: hidden;
}

.close:hover{
  color:red;
}

